* {
	&::-webkit-scrollbar-thumb {
		background: #5c5c5c;
		background-clip: padding-box;
		border: 3px solid rgba( 0, 0, 0, 0 );
		border-radius: 10px;
	}
	&::-webkit-scrollbar-track {
		background: #d6d6d6;
	}
	&::-webkit-scrollbar {
		width: 10px;
	}
}

body {
	margin: 0;
	
	overflow-y: auto;

	font-family: 'Roboto', sans-serif;
	font-size: 15px;
}

details {
	width: 300px;

	summary {
		background-color: #2b2b2b;
		padding: 10px;
		
		color: #fff;

		cursor: pointer;
	}
}

table {
	width: 100%;
	border: none;
	border-collapse: collapse;

	tr {
		&:has( td ):not( :last-child ) {
			border-bottom: 1px solid #e9e9e9;
		}

		th, td {
			padding: 6px 10px;

			text-align: left;
		}
		td {
			padding-top: 0;

			img {
				width: 100%;
				height: auto;
			}
		}
	}
}